import React from 'react';
import { Container, Row, Col } from 'react-bootstrap'; 
import '../styles/global.css'; 
import { withPrefix } from 'gatsby';

const Sponsors = () => {

  const sponsorData = [
    { name: 'Sponsor A', logo: '/images/sponsors/logo-ut.png', link: 'https://www.utwente.nl/' }/*,
    { name: 'Sponsor B', logo: '/images/sponsors/sponsor-b.png', link: 'https://www.sponsorB.com' },
    { name: 'Sponsor C', logo: '/images/sponsors/sponsor-c.png', link: 'https://www.sponsorC.com' },
    { name: 'Sponsor D', logo: '/images/sponsors/sponsor-d.png', link: 'https://www.sponsorD.com' },
    { name: 'Sponsor E', logo: '/images/sponsors/sponsor-e.png', link: 'https://www.sponsorE.com' },
    { name: 'Sponsor F', logo: '/images/sponsors/sponsor-f.png', link: 'https://www.sponsorF.com' },*/

  ];

  return (
    <section id="sponsors" className="sponsors-section py-5">
      <Container>
        <h2 className="text-center mb-2">Sponsors (TBD)</h2>
        <p className='text-center lead'>Contact us if you are interested in sponsoring ArtMetrics</p>
        <Row className="justify-content-center align-items-center">
          {sponsorData.map((sponsor, index) => (
            <Col key={index} xs={6} sm={4} md={3} lg={2} className="sponsor-logo-col text-center my-3">
              <a href={sponsor.link} target="_blank" rel="noopener noreferrer">
                <img
                  src={withPrefix(sponsor.logo)}
                  alt={sponsor.name}
                  className="img-fluid sponsor-logo"
                />
              </a>
            </Col>
          ))}
        </Row>
      </Container>
    </section>
  );
};

export default Sponsors;